<template>
  <div class="col-12 col-sm-6 col-lg-4  item-card">

    <div class="item-card-main-content">
      <div class="image-container item-card-header">      
        <img :src="product.imgsrc" />
      </div>
      <div class="item-card-footer">
        <div class="item-card-footer__title-wrap">
          <div class="item-card-footer__title">
            {{product.title}} 
          </div>
          <div class="item-card-footer__price">
            {{product.price}}
          </div>
        </div>  
        <div>
          <img class="item-card-footer__btn" src="@/assets/addBtn.png"  @click="toggleDescr(product.id)"/>
        </div>      
      </div>
    </div>

    <div class="item-card-descr" :class="{'item-card-descr--visible' : descrVisible}" >
      <div class="item-card-descr__close-btn" @click="toggleDescr()">закрыть</div>
      <div class="item-card-descr__content">
        <h3 class="item-card-descr__title">{{product.title}}</h3>
        <div class="item-card-descr__label">Производитель</div>
        <div class="item-card-descr__text">{{product.descr}}</div>
        <div class="item-card-descr__label">Описание</div>
        <div class="item-card-descr__text">{{product.type}}</div>
      </div>
    </div>

  </div>  
</template>

<script>
export default {
  name: "ItemCard",
  props: {
    product: Object,
    visibleCard: Number,
  },
  data() {
    return {
      descrVisible: false,
    };
  },
  computed: {},
  watch: {
    visibleCard(newVal) {
      if (newVal !== this.product.id) {
        this.descrVisible = false;
      }
    },
  },
  methods: {
    toggleDescr(val) {
      this.descrVisible = !this.descrVisible;
      if (val) {
        this.$emit("itemCardToggle", val);
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "@/assets/styles/settings.scss";

.item-card {
  border: 1px solid $gray-color;
  overflow: hidden;
}
.item-card:hover {
  background-color: $card-hover-color;
}

.item-card-descr {
  background-color: $white-color;
  position: absolute;
  z-index: 10;
  top: 0;
  left: -1000px;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.5s;
  color: $black-color;
  display: flex;
  justify-content: center;
  align-items: center;
}
.item-card-descr--visible {
  left: 0;
  opacity: 1;
  animation: slideIn 0.5s;
}

.item-card-main-content {
  width: 100%;
  height: 100%;
  position: relative;
}

.item-card-descr__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 1.4rem;
  cursor: pointer;
}
.item-card-descr__content {
  padding: 20px;
  margin: 40px auto;
}
.item-card-descr__title {
  font-size: 2.5rem;
}
.item-card-descr__label {
  color: $light-gray-color;
  font-size: 1.8rem;
  padding: 5px 0;
}
.item-card-descr__text {
  font-size: 1.4rem;
  padding: 5px 0;
}

.item-card-main-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  //flex-flow: row wrap;
  //align-content: space-between;
}
.item-card-header img {
  margin: 80px auto;
}
.item-card-footer {
  width: 100%;
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-card-footer__title {
  font-size: 2rem;
  font-weight: bold;
}
.item-card-footer__price {
  font-size: 1.5rem;
  color: $light-gray-color;
}
.item-card-footer__btn {
  cursor: pointer;
}

@keyframes slideIn {
  from {
    transform: rotateY(90deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
@keyframes slideOut {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(90deg);
  }
}
</style>
