<template>
  <div class="home">
    <app-header /> 

    <div class="container">     
      <div class="row">
        <div class="col-12 col-lg-3">
          <SidePanel @sideMenuClick="showProductByType($event)" />               
        </div>

        <div class="col-12 col-lg-9">
          <div class="row">
              <ItemCard  v-for="(product, index) in filteredProducts"
                :key="index"
                :product="product"
                :visibleCard="visibleCard"
                @itemCardToggle="onItemCardToggle"
                />     
          </div>
        </div>
      </div>      
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Header from "@/components/Header.vue";
import ItemCard from "@/components/ItemCard.vue";
import SidePanel from "@/components/SidePanel.vue";

export default {
  name: "home",
  components: {
    ItemCard,
    "app-header": Header,
    SidePanel,
  },
  data() {
    return {
      visibleCard: 0,
      products: [
        {
          id: 1,
          imgsrc: require("@/assets/stone.png"),
          title: "Щебень 20х40",
          price: "1800 тг",
          descr:
            "ТОО «СК-ГРАНТ»,  Северо-Казахстанская область, Тайыншинский район, карьер «Золоторудный».",
          type: "1-класс ГОСТ 8267-93",
          category: "stone",
        },
        {
          id: 2,
          imgsrc: require("@/assets/stone.png"),
          title: "Щебень 40х70",
          price: "1550 тг",
          descr:
            "ТОО «СК-ГРАНТ»,  Северо-Казахстанская область, Тайыншинский район, карьер «Золоторудный».",
          type: "1-класс ГОСТ 8267-93",
          category: "stone",
        },
        {
          id: 3,
          imgsrc: require("@/assets/stone.png"),
          title: "Щебень 5х20",
          price: "2000 тг",
          descr:
            "ТОО «СК-ГРАНТ»,  Северо-Казахстанская область, Тайыншинский район, карьер «Золоторудный».",
          type: "1-класс ГОСТ 8267-93",
          category: "stone",
        },
        {
          id: 4,
