          type: "1-класс ГОСТ 8267-93",
          category: "sand",
        },
      ],
      filter: "all",
    };
  },
  computed: {
    filteredProducts() {
      if (this.filter !== "all") {
        return this.products.filter(item => item.category === this.filter);
      } else {
        return this.products;
      }
    },
  },
  methods: {
    onItemCardToggle(val) {
      this.visibleCard = val;
    },
    showProductByType(type) {
      this.filter = type;
    },
  },
};
</script>
